import React, { useState, useEffect } from 'react'
import { Loading, Modal } from './Components'
import moment from 'moment'
import { getUsername, getToken } from './modules/storageHelper'
import { useParams } from 'react-router-dom'
import EventsViewMobile from './EventsViewMobile'
import EventsViewDesktop from './EventsViewDesktop'
import EventsToolsMobile from './EventsToolsMobile'
import EventsToolsDesktop from './EventsToolsDesktop'
import EventsDeleteButton from './EventsDeleteButton'

const ep = 'https://jadwalsetda.herokuapp.com/api/events/'

function Events(props) {
  const { type } = useParams()

  // data
  const [events, setEvents] = useState([])
  const [places, setPlaces] = useState([])

  // filters
  const [month, setMonth] = useState(moment().local().month() + 1)
  const [year, setYear] = useState(moment().local().year())
  const [searchVal, setSearchVal] = useState('')

  const [isLoading, setIsLoading] = useState(false)
  const [isModalVisible, setIsModalVisible] = useState(false)
  const [selectedEvent, setSelectedEvent] = useState(null)

  const [screenSize, setScreenSize] = useState({
    width: window.innerWidth,
    height: window.innerHeight,
  })

  let title
  switch (type) {
    case 'setda':
      title = ' Gedung & Ruang Rapat'; break;
    case 'adipura':
      title = ' Gedung Adipura'; break;
    case 'bupati':
      title = ' Bupati'; break;
    case 'wakil':
      title = ' Wakil Bupati'; break;
    case 'sekda':
      title = ' Sekretaris Daerah'; break;
    default: 
      title = ' Kegiatan'; break;
  }

  let printEp = 'https://jadwalsetda.herokuapp.com/print.html?'
  if (type) printEp += `type=${type}`
  let fromLc, toLc
  if (month && year) {
    fromLc = moment(`${year})-${month}`, 'YYYY-MM').startOf('month').local().format('YYYY-MM-DD HH:mm')
    toLc = moment(`${year})-${month}`, 'YYYY-MM').endOf('month').local().format('YYYY-MM-DD HH:mm')
    printEp += `&from=${fromLc}&to=${toLc}`
  }

  function getEvents() {
    setEvents([])
    setIsLoading(true)

    let from, to
    let epx = ep + '?'
    if (type) epx += `type=${type}`
    if (month && year) {
      from = moment(`${year})-${month}`, 'YYYY-MM').startOf('month').utc().format('YYYY-MM-DD HH:mm')
      to = moment(from, 'YYYY-MM-DD HH:mm').add(1, 'months').endOf('month').utc().format('YYYY-MM-DD HH:mm')
      epx += `&from=${from}&to=${to}`
    }

    fetch(epx)
      .then(res => res.json())
      .then(docs => {
        setIsLoading(false)
        setEvents(docs)
      })
      .catch(err => {
        console.log(err)
        setIsLoading(false)
      })
  }

  function getPlaces() {
    fetch('https://jadwalsetda.herokuapp.com/api/places')
      .then(res => res.json())
      .then(docs => {
        setPlaces(docs)
      })
      .catch(err => {
        console.log(err)
      })
  }

  useEffect(() => {
    getEvents()
    getPlaces()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [type, month, year])

  useEffect(() => {
    function updateScreenSize() {
      setScreenSize({
        width: window.innerWidth,
        height: window.innerHeight,
      })
    }
    window.addEventListener('resize', updateScreenSize)
    return () => window.removeEventListener('resize', updateScreenSize)
  }, [setScreenSize])

  function match(c) {
    return c
      .toLowerCase()
      .indexOf(searchVal.toLowerCase())
      > -1
  }

  function showModal(data) {
    setIsModalVisible(true)
    if(data) setSelectedEvent(data)
  }

  return (
    <>
      <div className="title">Jadwal {title}</div>

      {screenSize.width <= 768 ?
        <EventsToolsMobile
          printEp={printEp}
          showModal={showModal}
          month={month}
          setMonth={setMonth}
          year={year}
          setYear={setYear}
        />
        :
        <EventsToolsDesktop
          printEp={printEp}
          showModal={showModal}
          month={month}
          setMonth={setMonth}
          year={year}
          setYear={setYear}
          searchVal={searchVal}
          setSearchVal={setSearchVal}
        />
      }

      {isLoading ? <Loading /> : null}

      {!isLoading && !events.length ?
        <div className="box">Tidak ada data...</div>
        : null
      }

      {events.length ?
        screenSize.width <= 768 ?
          <EventsViewMobile
            events={events}
            match={match}
            showModal={showModal}
          />
          :
          <EventsViewDesktop
            events={events}
            match={match}
            showModal={showModal}
          />
        : null
      }

      {isModalVisible ?
        <SetEvent
          type={type}
          event={selectedEvent}
          getEvents={getEvents}
          closeModal={() => {
            setSelectedEvent(null)
            setIsModalVisible(false)
          }}
          notify={props.notify}
          places={places}
        />
        : null
      }

    </>
  )
}

function SetEvent(props) {
  const id = props.event ? props.event._id : null
  const type = props.type || 'invalid'
  const admin = getUsername() || 'no-id'

  const { event } = props

  const [name, setName] = useState(event ? event.name : '')
  const [place, setPlace] = useState(event ? event.place : '')
  const [participant, setParticipant] = useState(event ? event.participant : 0)
  const [pic, setPic] = useState(event ? event.pic : '')
  const [notes, setNotes] = useState(event ? event.notes : '')
  const [date, setDate] = useState(event ?
    moment(event.dateTime).format('YYYY-MM-DD')
    :
    moment().format('YYYY-MM-DD')
  )
  const [time, setTime] = useState(event ?
    moment(event.dateTime).format('HH:mm')
    :
    '08:00'
  )

  const [isLoading, setIsLoading] = useState(false)

  function handleSubmit(e) {
    const epx = id ? ep + id : ep
    const method = id ? 'PUT' : 'POST'
    const headers = {
      'Accept': 'application/json',
      'Content-Type': 'application/json',
      'Authorization': 'Bearer ' + getToken(),
    }
    const body = JSON.stringify({
      name: name,
      place: place,
      dateTime: moment(`${date} ${time}`, 'YYYY-MM-DD HH:mm').utc().format('YYYY-MM-DD HH:mm'),
      participant: participant,
      pic: pic,
      type: type,
      admin: admin,
      notes: notes,
    })

    setIsLoading(true)
    fetch(epx, {
      method: method,
      headers: headers,
      body: body,
    }).then(() => {
      props.notify('berhasil menyimpan kegiatan')
      props.getEvents()
      props.closeModal()
    }).catch(err => {
      props.closeModal()
      console.log(err)
    })
    e.preventDefault()
  }

  return (
    <Modal closeModal={props.closeModal}>

      <h1 className="title">
        {id ? 'Ubah ' : 'Tambah '}
        Kegiatan
      </h1>

      <form onSubmit={handleSubmit}>

        <div className="field">
          <label className="label">Kegiatan/Acara</label>
          <div className="control">
            <input
              className="input"
              type="text"
              value={name}
              onChange={e => setName(e.target.value)}
              required
            />
          </div>
        </div>

        {type === 'adipura' ?
          null
          :
          <div className="field">
            <label className="label">Tempat/Lokasi</label>
            <div className="control">
              <input
                className="input"
                type="text"
                value={place}
                onChange={e => setPlace(e.target.value)}
                list="places"
              />
              <datalist id="places">
                {props.places ?
                  props.places.map(place =>
                    <option key={place._id} value={place.name} />
                  )
                  : null
                }
              </datalist>
            </div>
          </div>
        }

        {type === 'setda' || type === 'adipura' ?
          <div className="field">
            <label className="label">Unit Kerja</label>
            <div className="field is-grouped">

              <div className="control">
                <input
                  className="input"
                  type="text"
                  value={pic}
                  onChange={e => setPic(e.target.value)}
                />
              </div>

              {type !== 'adipura' ?
                <div className="control">
                  <input
                    className="input"
                    style={{ width: '5rem' }}
                    type="number"
                    value={participant}
                    onChange={e => setParticipant(e.target.value)}
                  />
                </div>
                : null
              }

            </div>
          </div>
          : null
        }

        <div className="field">
          <label className="label">Waktu</label>

          <div className="field is-grouped">
            <div className="control has-icons-left">
              <input
                className="input"
                type="date"
                value={date}
                onChange={e => setDate(e.target.value)}
                required
              />
              <span className="icon is-small is-left">
                <i className="fas fa-calendar" />
              </span>
            </div>

            <div className="control has-icons-left">
              <input
                className="input"
                type="time"
                value={time}
                onChange={e => setTime(e.target.value)}
                required
              />
              <span className="icon is-small is-left">
                <i className="fas fa-clock" />
              </span>
            </div>
          </div>

        </div>

        <div className="field">
          <label className="label">Catatan</label>
          <div className="control">
            <textarea
              className="textarea"
              value={notes}
              onChange={e => setNotes(e.target.value)}
            />
          </div>
        </div>

        <div className="field is-grouped">

          <div className="control">
            <button
              type="submit"
              className={
                `button is-primary
                ${isLoading ? ' is-loading' : ''}
                `
              }
            >Simpan</button>
          </div>

          {id ?
            <div className="control">
              <EventsDeleteButton id={id}
                getEvents={props.getEvents}
                closeModal={props.closeModal}
                notify={props.notify}
              />
            </div>
            : null
          }

          <div className="control">
            <button
              type="button"
              className="button is-text"
              onClick={props.closeModal}
            >Batal</button>
          </div>

        </div>

      </form>

    </Modal>
  )
}

export default Events