import React from 'react'
import moment from 'moment'
import { getRole } from './modules/storageHelper'
import { useParams } from 'react-router-dom'

const range = (start, stop, step) => Array.from(
  { length: (stop - start) / step + 1 },
  (_, i) => start + (i * step)
)

function EventsToolsDesktop(props) {
  const role = getRole()
  const { type } = useParams()

  return (
    <div className="level">
      <div className="level-left">
        <div className="level-item">
          <div className="field has-addons">

            {/* SELECT MONTH */}
            <div className="control">
              <div className="select">
                <select value={props.month}
                  onChange={e => props.setMonth(parseInt(e.target.value))}
                >
                  {range(1, 12, 1).map(m =>
                    <option key={m} value={m}>
                      {moment(m, 'M').format('MMMM')}
                    </option>
                  )}
                </select>
              </div>
            </div>

            {/* SELECT YEAR */}
            <div className="control">
              <div className="select">
                <select value={props.year}
                  onChange={e => props.setYear(parseInt(e.target.value))}
                >
                  {range(2019, moment().add(3, 'years').year(), 1).map(y =>
                    <option key={y} value={y}>{y}</option>
                  )}
                </select>
              </div>
            </div>
          </div>
        </div>
        <div className="level-item">
          <div className="field has-addons">
            <div className="control">
              <input type="text"
                className="input"
                placeholder="ketik untuk mencari..."
                value={props.searchVal}
                onChange={e => props.setSearchVal(e.target.value)}
              />
            </div>
            <div className="control">
              <button type="button"
                className={`button${props.searchVal ? ' is-danger' : ''}`}
                onClick={() => props.setSearchVal('')}
              >
                <span className="icon">
                  <i className="fas fa-backspace" />
                </span>
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="level-right">

        {/* NEW ITEM (PLUS SIGN) */}
        {role === 'super' || role === type ?
          <div className="level-item">
            <button className="button is-link"
              onClick={() => props.showModal()}
            >
              <span className="icon">
                <i className="fas fa-plus" />
              </span>
              <span>Tambah</span>
            </button>
          </div>
          : null
        }

        <div className="level-item">
          <a target="_blank"
            rel="noopener noreferrer"
            className="button is-primary"
            href={props.printEp}
          >
            <span className="icon">
              <i className="fas fa-print" />
            </span>
            <span>Cetak</span>
          </a>
        </div>
      </div>
    </div>
  )
}

export default EventsToolsDesktop