import React, { useState, useEffect } from 'react'
import moment from 'moment'
import { Loading } from './Components'
import { Link } from 'react-router-dom'
import { getIsObserver } from './modules/storageHelper'

function Home() {
  const ep = 'https://jadwalsetda.herokuapp.com/api/events/'
  const isObserver = getIsObserver()

  const [events, setEvents] = useState([])
  const [isLoading, setIsLoading] = useState(false)

  function getEvents() {
    const type = 'setda'
    const from = moment().startOf('day').utc().format('YYYY-MM-DD HH:mm')
    const to = moment().endOf('day').utc().format('YYYY-MM-DD HH:mm')
    const epx = ep + `?type=${type}&from=${from}&to=${to}`

    setIsLoading(true)
    fetch(epx)
      .then(res => res.json())
      .then(docs => {
        setIsLoading(false)
        setEvents(docs)
      })
      .catch(err => {
        setIsLoading(false)
        console.log(err)
      })
  }

  useEffect(() => {
    getEvents()
  }, [])

  return (
    <>
      <div className="title">Jadwal Hari Ini</div>

      {isLoading ? <Loading /> : null}

      {events.length ?
        <div className="evm">
          {events.map(event => {
            return (
              <div key={event._id} className="evm-item">
                <div className="evm-left">
                  <div className="evm-date">
                    {event.dateTime ?
                      moment(event.dateTime)
                        .local()
                        .format('D MMM')
                      : null
                    }
                    <br />
                    {event.dateTime ?
                      moment(event.dateTime)
                        .local()
                        .format('YYYY')
                      : null
                    }
                  </div>
                  <div className="evm-time">
                    {event.dateTime ?
                      moment(event.dateTime)
                        .local()
                        .format('HH:mm')
                      : null
                    }
                  </div>
                </div>

                <div className="evm-right">
                  <div className="evm-name">{event.name}</div>
                  <div className="evm-details">

                    {event.place ?
                      <div className="evm-detail">
                        <div className="evm-icon">
                          <i className="fas fa-map-marker-alt" />
                        </div>
                        {event.place}
                      </div>
                      : null
                    }

                    {event.pic ?
                      <div className="evm-detail">
                        <div className="evm-icon">
                          <i className="fas fa-briefcase" />
                        </div>
                        {event.pic}
                        {event.participant ?
                          ` - ${event.participant}`
                          : null
                        }
                      </div>
                      : null
                    }

                    {event.notes ?
                      isObserver === 'true' ?
                        <div className="evm-detail">
                          <div className="evm-icon">
                            <i className="fas fa-clipboard" />
                          </div>
                          {event.notes}
                        </div>
                        : null
                      : null
                    }

                  </div>
                </div>
              </div>
            )
          })}
        </div>
        : null
      }

      {isLoading ? null :
        <div style={{ margin: '1rem 0' }}>
          <button className="button">
            <Link to="/events/setda">Selengkapnya...</Link>
          </button>
        </div>
      }
    </>
  )
}

export default Home
