import React from 'react'
import moment from 'moment'
import { useParams } from 'react-router-dom'
import { getRole } from './modules/storageHelper'

const range = (start, stop, step) => Array.from(
  { length: (stop - start) / step + 1 },
  (_, i) => start + (i * step)
)

function EventsToolsMobile(props) {
  const { type } = useParams()
  const role = getRole()

  return (
    <div className="tm">
      <div className="field is-grouped">
        
        {/* select month */}
        <div className="control">
          <div className="select is-small">
            <select value={props.month}
              onChange={e => props.setMonth(parseInt(e.target.value))}
            >
              {range(1, 12, 1).map(m =>
                <option key={m} value={m}>
                  {moment(m, 'M').format('MMMM')}
                </option>
              )}
            </select>
          </div>
        </div>

        {/* select year */}
        <div className="control">
          <div className="select is-small">
            <select value={props.year}
              onChange={e => props.setYear(parseInt(e.target.value))}
            >
              {range(2019, moment().add(3, 'years').year(), 1).map(y =>
                <option key={y} value={y}>{y}</option>
              )}
            </select>
          </div>
        </div>
      </div>

      <div className="tm-buttons">
        {role === 'super' || role === type ?
          <button className="button is-small is-link"
            onClick={() => props.showModal()}
          >
            <span className="icon">
              <i className="fas fa-plus" />
            </span>
          </button>
          : null
        }
        <a target="_blank"
          rel="noopener noreferrer"
          href={props.printEp}
          className="button is-small is-primary"
        >
          <span className="icon">
            <i className="fas fa-print" />
          </span>
        </a>
      </div>
    </div>
  )
}

export default EventsToolsMobile