import React, { useState } from 'react'
import { getToken } from './modules/storageHelper'

function EventsDeleteButton(props) {
  const ep = 'https://jadwalsetda.herokuapp.com/api/events/'

  const [isLoading, setIsLoading] = useState(false)
  const [isConfirming, setIsConfirming] = useState(false)

  function handleDelete() {
    if (!isConfirming) return setIsConfirming(true)
    setIsLoading(true)
    fetch(ep + props.id, {
      method: 'DELETE',
      headers: { 'Authorization': 'Bearer ' + getToken() }
    }).then(() => {
      props.getEvents()
      props.notify('Berhasil menghapus kegiatan')
      props.closeModal()
    }).catch(err => {
      console.log(err)
      props.closeModal()
    })
  }

  return (
    <button type="button"
      onClick={handleDelete}
      className={
        `button
          ${!isConfirming ? ' is-warning' : 'is-danger'}
          ${isLoading ? ' is-loading' : ''}
        `
      }
    >
      {!isConfirming ? 'Hapus' : 'Hapus Permanen!'}
    </button>
  )
}

export default EventsDeleteButton