import React from 'react'
import moment from 'moment'
import { getIsObserver, getRole } from './modules/storageHelper'
import { useParams } from 'react-router-dom'

function EventsViewMobile(props) {
  const { type } = useParams()
  const isObserver = getIsObserver()
  const role = getRole()
  const events = props.events

  return (
    <div className="evm">
      {events.map(event => {
        return (
          <div
            key={event._id}
            onClick={() => {
              if(role === 'super' || role === type)
                props.showModal(event)
            }}
            className={`evm-item${
              moment().local().format('YYYY-MM-DD') ===
                moment(event.dateTime).local().format('YYYY-MM-DD') ?
                ' is-today'
                : ''
              }
              `
            }
          >

            <div className="evm-left">
              <div className="evm-date">
                <>
                  {event.dateTime ?
                    moment(event.dateTime)
                      .local()
                      .format('D MMM')
                    : null
                  }
                  <br />
                  {event.dateTime ?
                    moment(event.dateTime)
                      .local()
                      .format('YYYY')
                    : null
                  }
                </>
              </div>
              <div className="evm-time">
                {event.dateTime ?
                  moment(event.dateTime)
                    .local()
                    .format('HH:mm')
                  : null
                }
              </div>
            </div>

            <div className="evm-right">
              <div className="evm-name">{event.name}</div>
              <div className="evm-details">

                {event.place ?
                  <div className="evm-detail">
                    <div className="evm-icon">
                      <i className="fas fa-map-marker-alt" />
                    </div>
                    {event.place}
                  </div>
                  : null
                }

                {event.pic ?
                  <div className="evm-detail">
                    <div className="evm-icon">
                      <i className="fas fa-briefcase" />
                    </div>
                    {event.pic}
                    {event.participant ?
                      ` - ${event.participant}`
                      : null
                    }
                  </div>
                  : null
                }

                {event.notes ?
                  isObserver === 'true' ?
                    <div className="evm-detail">
                      <div className="evm-icon">
                        <i className="fas fa-clipboard" />
                      </div>
                      {event.notes}
                    </div>
                    : null
                  : null
                }

              </div>
            </div>

          </div>
        )
      }
      )}
    </div>
  )
}

export default EventsViewMobile